/* a partir de aca */

.graph{
  display:flex;

}

.switcher {
	display: flex;
	align-items: center;
	height: 30px;
	margin-top: 8px;
	color: #2196F3;
}

.switcher-item {
	cursor: pointer;
	text-decoration: none;
	display: inline-block;
	padding: 6px 8px;
	font-size: 14px;
	color: #262b3e;
	background-color: transparent;
	margin-right: 8px;
	border: none;
	border-radius: 4px;
	outline: none;
}

.switcher-item:hover {
	background-color: #f2f3f5;
}

.switcher-active-item {
	text-decoration: none;
	cursor: default;
	color: #262b3e;
}

.switcher-active-item,
.switcher-active-item:hover {
	background-color: #e1eff9;
}


@media only screen and (min-width: 1101px) {
  /* For mobile phones: */

  * {
    box-sizing: border-box;
  }

  .grid {
    display: grid;
    /* display: inline-grid */
    grid-template-columns: 30% 30% 40%;
    /* grid-template-columns: 1fr 1fr 1fr ; */
    /* grid-template-columns: repeat(3, 1fr) ; */
    /* grid-template-rows: 200px 400px; */
    /* grid-template-rows: auto ; */
    grid-template-rows: minmax(auto, auto);
    /* gap: 0.5em; */
    justify-items: stretch;
    align-items: stretch;
  }

  .grid .e-1 {
    padding: 1em 1em;
    display: grid;
    /* grid-template-columns: min-content; */
    grid-template-rows: 70% 30%;
  }

  .grid .e-1-1 {
    padding: 0px;
    /* grid-column-start: 1;
    grid-column-end: 3; */
    font-size: 3em;
    display: flex;
    align-items: center;
  }

  .grid .e-1-2 {
    padding: 0;
    display: flex;
    align-items: center;
    font-size: 1.7em;
    /* justify-content: center; */
  }

  .grid .e-2 {
    display: grid;
    grid-template-columns: min-content min-content;
    grid-template-rows: 70% 30%;
    padding: 1em 1em;
  }

  .grid .e-2-1 {
    font-size: 3em;
    padding: 0;
    display: flex;
    align-items: center;
  }

  .grid .e-2-2 {
    font-size: 2em;
    padding: 0.5em;
    display: flex;
    align-items: center;
  }

  .grid .e-2-3 {
    font-size: 1.7em;
    padding: 0;
    display: flex;
    align-items: center;
  }

  .grid .e-2-4 {
    font-size: 0.9em;
    padding: 1em;
    display: flex;
    align-items: center;
  }

  .e-3 {
    display: grid;
    grid-template-columns: 35% 65%;
    /* grid-template-rows: ; */
  }

  .grid .e-3-1 {
    padding: 0;
    display: flex;
    align-items: center;
  }

  .grid .e-3-2 {
    padding: 0;
    display: flex;
    align-items: center;
  }

  .grid .e-4 {
    grid-column-start: 1;
    grid-column-end: 4;
  }

  .e-5 {
    grid-column-start: 1;
    grid-column-end: 4;
  }

  .e-5-1 {
   display: grid;
  grid-template-columns: repeat(7, 1fr);
  }

  .grid .e-5-2 {
    display: flex;
    justify-content: center;
   }
  .e-6 {
    grid-column-start: 1;
    grid-column-end: 4;
  }

  .grid div {
    padding: 1em;
  }

  div:hover {
    border: 1px solid green;
  }
}

